export const get = (endpoint, headers, payload) => {
  if (endpoint === undefined)
    throw new Error("Cannot use GET without endpoint.");

  return request("GET", endpoint, headers, payload);
};

export const post = (endpoint, headers, payload) => {
  if (endpoint === undefined)
    throw new Error("Cannot use POST without endpoint.");

  return request("POST", endpoint, headers, payload);
};

export const request = (method, endpoint, headers, body) => {
  return fetch(endpoint, {
    headers,
    method,
    body,
    mode: "cors"
  }).then(response => response.json());
};

export class Apicon {
  constructor(endpointAddress) {
    let _data;
    let _headers;
    let _payload;
    let _endpoint = endpointAddress;

    this.setData = data => (_data = data);
    this.getData = () => _data;

    this.setHeaders = headers => (_headers = headers);
    this.getHeaders = () => _headers;

    this.setPayload = payload => (_payload = payload);
    this.getPayload = () => _payload;

    this.setEndpoint = endpoint => (_endpoint = endpoint);
    this.getEndpoint = () => _endpoint;

    this.get = (argEnpoint, argHeaders, argPayload) => {
      let localEnpoint = argEnpoint || this.getEndpoint();
      let localHeaders = argHeaders || this.getHeaders();
      let localPayload = argPayload || this.getPayload();

      this.setEndpoint(localEnpoint);

      return get(localEnpoint, localHeaders, localPayload).then(data => {
        this.setData(data);
        return data;
      });
    };

    this.post = (argEnpoint, argHeaders, argPayload) => {
      let localEnpoint = argEnpoint || this.getEndpoint();
      let localHeaders = argHeaders || this.getHeaders();
      let localPayload = argPayload || this.getPayload();

      return post(localEnpoint, localHeaders, localPayload).then(data => {
        this.setDataGET(data);
        return data;
      });
    };
  }
}
